<template>
  <div class="header-top">
    <div class="top">
      <div class="top_main">
        <div class="top23">
          <div>快速配送</div>
          <div @click="ddzt">到店自提</div>
        </div>
        <div class="topbottom">
          <van-contact-card
            type="edit"
            :name="list[1].name"
            :tel="list[1].tel"
            @click="onEdit2"
          />


          <van-popup v-model="show" closeable position="bottom">
            <van-address-list
              v-show="show"
              v-model="chosenAddressId"
              :list="list"
              default-tag-text="默认"
              @add="onAdd"
            />
          
          </van-popup>
        </div>
      </div>
    </div>
    <div class="cartNUn">
      <p>共{{ cartnum }}件商品</p>
      <ul class="catnul">
        <li v-for="i in cartNU" :key="i.id">
          <img :src="i.productInfo.image" alt="" />
          <div class="cartright">
            <div class="cartto">
              <span>{{ i.productInfo.store_name }}</span>
              <span>X{{ i.cart_num }}</span>
            </div>
            <span class="sku1">{{ i.productInfo.attrInfo.suk }}</span>
            <span class="shop_price">￥{{ i.productInfo.price }}</span>
          </div>
        </li>
      </ul>
    </div>

    <van-cell-group>
      <van-coupon-cell @click="showList1 = true" />
      <van-popup
        v-model="showList1"
        round
        position="bottom"
        style="height: 90%; padding-top: 4px"
      >
        <van-coupon-list :chosen-coupon="chosenCoupon" />
      </van-popup>
      <van-cell title="单元格">
        <div class="danyuange">
          <span>积分抵扣</span><span>0.00</span
          ><van-checkbox class="wuzi" v-model="checked"></van-checkbox>
        </div>
      </van-cell>
      <van-cell title="快递费" value="免运费" />
      <van-cell title="备注信息" class="input">
        <textarea  class="text_rea" placeholder="请添加备注"></textarea>
      </van-cell>
    </van-cell-group>

    <div class="payment">
      <span>支付方式</span>
      <div class="zhifu2">
         <div ref="wechat" @click="addact2">
          <span>微信支付</span>
          <span>微信快捷支付</span>
        </div>

        <div ref="doll" class="active" @click="addact">
          <span>余额支付</span>
          <span>可用余额:{{my.now_money}}</span>
        </div>
       
      </div>
    </div>

    <div class="shangpin">
      <span>商品总价</span>
      <span>￥{{ carallnum }}.00</span>
    </div>
    <van-submit-bar
      :price="carallnum * 100"
      button-text="立刻结算"
      @submit="settlement"
    />
    <div class="nav"></div>
  </div>
</template>
<script>
import { mapGetters, mapMutations } from "vuex";
import { Toast } from "vant";
export default {
  name: "paymentsuccessful",
  data() {
    return {
      chosenAddressId: "1",
      list: [ {
          id: '1',
          name: '张三',
          tel: '13000000000',
          address: '浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室',
          // isDefault: true,
         
        },],
    
      show: false,
      cartNU: [],
      cartnum: 0,
      checked: true,
      carallnum: 0,
      //优惠券
      showList1: false,
      chosenCoupon: -1,
      arr3: [],
      addcar: null,
      jsd:null,
      ord:[],
      my:'',
    };
  },
  created() {
    console.log("购物车提交数据");
    console.log(this.$store.state.cartNU)
    this.cartNU = this.$store.state.cartNU;

    for (let i in this.cartNU) {
      this.cartnum += this.cartNU[i].cart_num;
      this.carallnum +=
        this.cartNU[i].productInfo.price * this.cartNU[i].cart_num;
      // console.log(this.cartNU[i].id);
    }

      this.$axios.get('http://47.115.51.185/api/user').then(d=>{
        this.my=d.data.data;
        console.log('金额',d)
    })

    console.log(this.cartNU);

          //  地址
        this.$axios
      .get(`http://47.115.51.185/api/address/list?page=1&limit=20`)
      .then((d) => {
        console.log(d);
        //   this.$axios.get(`http://47.115.51.185/api/address/list?page=1&limit=20`).then(d=>{
        d.data.data.forEach((item,index) => {
          console.log(item,index);

              this.list.push({
                address:item.city,
                id:item.id,
                name:item.real_name,
                tel:item.phone
              })


            
        });
        console.log(this.list);

      });
  },
  methods: {
    settlement() {
      this.$axios
        .post("http://47.115.51.185/api/order/confirm", {
          cartId: String(this.cartNU[0].id) ,
        })
        .then((d) => {
          console.log("间隔数据111111111111111");
          console.log(d)
          this.ord = d.data.data.orderKey;
          console.log(this.ord);
          this.addcar = d.data.data.addressInfo.id;
        
          this.$axios
            .post("http://47.115.51.185/api/order/computed/" + this.ord, {
              addressId: this.addcar,
              couponId: 0,
              payType: "yue",
              shipping_type: 1,
              useIntegral: 0,
            })
            .then((d) => {
              console.log(d);
            });
          this.$axios
            .post("http://47.115.51.185/api/order/create/" + this.ord, {
              addressId: this.addcar,
              couponId: 0,
              payType: "yue",
              shipping_type: 1,
              useIntegral: 0,
            })
            .then((d) => {
              //数据
              console.log("1111111111111111");
              this.$axios
                .get(
                  "http://47.115.51.185/api/order/detail/" +
                  d.data.data.result.orderId
                
                )
                .then((d) => {
                  console.log(d.data.data.order_id);

                  this.$router.push({
                    path: "/paymentlist",
                    query: {
                      price: this.carallnum ,
                      // Ordernumber
                      Ordernumber:d.data.data.order_id

                    },
                  });
                });
            });
        });
    },
    
    onEdit2() {
      this.show = true;
    },
    addact() {
      this.$refs.wechat.classList.remove("active");
      this.$refs.doll.classList.add("active");
    },
    addact2() {
      this.$refs.doll.classList.remove("active");
      this.$refs.wechat.classList.add("active");
    },
    ddzt() {
      Toast("暂无门店信息,您无法选择到店自提");
    },
    onAdd() {
      this.$router.push("/dzi");
    },
    ...mapMutations(["addcart"]),
    ...mapMutations(["addshou"]),
    // ...mapGetters(["userInfo", "token"]),
  },
  computed: {
    ...mapGetters(["userInfo", "token"]),
  },
};
</script>
<style lang="less" scoped>
.header-top {

  height: 24vh;
  background-image: linear-gradient(180deg, #e93323, #f5f5f5);
  padding: 1px;
  .top_main {
    width: 90%;
    height: 24vh;
    margin-left: 5vw;
    margin-top: 3vh;

    .top23 {
      display: flex;
      justify-content: space-around;
      color: #fc4141;
      div:nth-child(1) {
        flex: 1;
        text-align: center;
        background-color: white;
        height: 6vh;
        line-height: 7vh;
       
      }
      div:nth-child(2) {
        display: block;
        margin-top: 2vh;
        height: 4vh;
        flex: 1;
        text-align: center;
        line-height: 4vh;
        background-color: #f7c1bd;
       
      }
    }
    .topbottom {
      background-color: white;
      // height: 12vh;
      background-color: white;
      border-top: 1px solid red;
    }
  }
}
.cartNUn {
  padding: 2px 3px;
  background-color: white;
  p {
    padding: 0px 4vw;
  }
}
.catnul {
  li {
    border-top: 1px solid #f0f0f0;
    padding: 10px 10px;
    display: flex;
    img {
      padding: 5px 5px;
      width: 24vw;
      height: 15vh;
      border-radius: 7px;
    }
    .cartright {
      flex: 1;
      display: flex;
      flex-direction: column;
      padding-left: 2vw;
      .cartto {
        display: flex;
        justify-content: space-between;
        span:nth-child(1) {
          display: block;
          text-overflow: ellipsis;
          overflow: hidden;
          width: 60vw;
          white-space: nowrap;
          color: black;
        }
        span:nth-child(2) {
          color: #868686;
          font-size: 14px;
        }
      }
      .sku1 {
        color: #868686;
        font-size: 14px;
      }
      .shop_price {
        padding-top: 6vh;
        color: red;
      }
    }
  }
}
.danyuange {
  display: flex;
  justify-content: flex-end;
  span:nth-child(1) {
    padding-right: 1vw;
  }
  span:nth-child(2) {
    color: red;
    padding-right: 4vw;
  }
}
.input {
  display: flex;
  flex-direction: column;
  textarea {
    background-color: #f9f9f9;
    width: 80vw;
    height: 14vh;
    padding: 1px 1px;
  }
}
.payment {
  padding: 20px 10px;
  .zhifu2 {
    div {
      margin: 2vh 10vw;
      width: 80vw;
      height: 8vh;
      border: 1px solid #eee;
      border-radius: 10px;
      display: flex;
      span {
        margin-top: 17px;
        height: 3vh;
        padding: 0px 15px;
        font-size: 14px;
      }
      span:nth-child(1) {
        display: flex;
        border-right: 1px solid #eee;
      }
    }
  }
}
.active {
  border: 1px solid red !important;
  span:nth-child(1) {
    color: red !important;
  }
}
.shangpin {
  border-top: 1px solid #f9f9f9;
  padding: 2vh 4vw;
  display: flex;
  justify-content: space-between;
}
.text_rea{
  border: #868686;

}
.input textarea{
  width: 100%;
}
.nav {
  padding-bottom: 55px;
}
</style>
